import { createArticle, getArticle, updateArticle } from '@/api/article'

export default {
    name: 'Editor',
    middleware: 'auth',
    data () {
        return {
            article: {
                title: '',
                description: '',
                body: '',
                tagList: []
            },
            curtag: '',
            disabled: false,
            errors: {} // 错误信息
        }
    },
    async mounted () {
        const slug = this.$route.params.slug
        if (slug) {
            this.slug = slug
            const { data } = await getArticle(slug)
            this.article = data.article
        }
    },
    methods: {
        savetags(){
          if(!this.article.tagList.includes(this.curtag)){
              this.article.tagList.push(this.curtag)
              this.curtag = ''
          }
        },
        deltag(index) {
            // const index = this.article.tagList.indexOf(item)
            this.article.tagList.splice(index,1)
        },
        async onSubmit () {
            this.disabled = true
            try {
                if(this.slug){
                    const { data } = await updateArticle(this.slug, {article: this.article})
                    this.$router.push(`/article/${data.article.slug}`)
                }else{
                    const { data } = await createArticle({
                        article: this.article
                    })
                    this.$router.push(`/article/${data.article.slug}`)
                    // 成功后跳转到该文章页面 /article/slug
                    // const { slug } = data
                    // this.$router.push({
                    //     name: 'article',
                    //     params: {
                    //         slug
                    //     }
                    // })
                }
                
            } catch (err) {
                this.disabled = false
                this.errors = err.response.data.errors
            }
        }
    }
}
